  button {
    width: 80px;
    height: 30px;
    margin:5px;
    border-radius: 7px;
    background-color: rgb(36, 206, 248);
    border-style: none;
    box-shadow: 0 3px #999;
  }

  button:hover {
    background-color: rgb(72, 212, 247);
    box-shadow: 0 10px 10px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
    cursor: pointer; /* Safari */
    transition-duration: 0.4s;
  }

  select {
    border-radius: 4px;
  }

  .col-25 *, .col-75 * {
    box-sizing: border-box;
  }

  /* * {
    box-sizing: border-box;
  } */

  .col-25 {
    float: left;
    width: 25%;
    margin-top: 6px;
  }

  .col-75 {
    float: right;
    width: 75%;
    margin-top: 6px;
  }
  .col-100 {
    float: left;
    width: 100%;
    margin-top: 6px;
    border-radius: 4px;
    background-color: transparent;
    padding: 0;
  }

  input[type=text], textarea {
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    resize: vertical;
  }

  label {
    padding: 12px 12px 12px 0;
    display: inline-block;
  }

  .displayRow:after {
    content: "";
    display: table;
    clear: both;
  }
  
  @media screen and (max-width: 600px) {
    .col-25, .col-75 {
      width: 100%;
      margin-top: 0;
      text-align: left;
    }
    button {
      width: 100%;
    }
  }
  

